import { isNumber, isDate } from "../common/data"

/**
 * 1020
 */
export function toHourStringFromMins(mins: number): string {
    if (mins < 0) {
        return ""
    }
    if (mins == 0) {
        return "0"
    }
    const h = parseInt((mins / 60).toString())
    const m = parseInt((mins % 60).toString())
    const s = []
    if (h > 0) {
        s.push(`${h}`)
    }
    if (m > 0) {
        s.push(`${m}`)
    }
    return s.join("")
}

/**
 * Date
 * (M)(d)(h)(m)(s)(q)  1-2 (y) 1-4 (S) 1 ( 1-3 )
 * @param dateValue 
 * @param fmtStr yyyy-MM-ddyyyy-MM-dd
 * @returns 
 */
export function format(dateValue: Date | number, fmtStr: string = "yyyy-MM-dd") {
    let fmt = fmtStr
    let dt: Date = dateValue as Date
    if (isNumber(dateValue as any)) {
        dt = new Date(dateValue)
    }
    if (!isDate(dt)) {
        return ""
    }
    const o: any = {
        "M+": dt.getMonth() + 1, //
        "d+": dt.getDate(), //
        "h+": dt.getHours(), //
        "m+": dt.getMinutes(), //
        "s+": dt.getSeconds(), //
        "q+": Math.floor((dt.getMonth() + 3) / 3), //
        S: dt.getMilliseconds() //
    }
    if (/(y+)/.test(fmt)) {
        fmt = fmt.replace(RegExp.$1, (dt.getFullYear() + "").substr(4 - RegExp.$1.length))
    }
    for (const k in o) {
        if (new RegExp("(" + k + ")").test(fmt)) {
            fmt = fmt.replace(RegExp.$1, RegExp.$1.length == 1 ? o[k] : ("00" + o[k]).substr(("" + o[k]).length))
        }
    }
    return fmt
}
