import { trim } from "./string"

/**
 * objnull
 */
export function isNull(obj: any) {
    return obj === null
}

/**
 * obj
 */
export function isArray(obj: any) {
    if (!obj) {
        return false
    }
    return Object.prototype.toString.call(obj) === "[object Array]"
}

/**
 * val
 * @param val 
 * @returns  
 */
export function isNumber(val: any) {
    return (typeof val === "number" || typeof val === "string") && val !== "" && !isNaN(val as any)
}

/**
 * 
 * @param  val 
 * @returns  
 */
export function isObject(val: any) {
    return !!(val && typeof val === "object")
}

/**
 * nullundefined""
 * @param  val 
 * @returns 
 */
export function isNullOrEmpty(val: any) {
    return isUndefined(val) || isNull(val) || val === ""
}

/**
 * nullundefined""
 * @param  val 
 * @returns 
 */
export function isNullOrWhiteSpace(val: any) {
    return isUndefined(val) || isNull(val) || trim(val) === ""
}

/**
 * function
 * @param  val 
 * @returns 
 */
export function isFunction(val: any) {
    return !!(val && Object.prototype.toString.call(val) == "[object Function]")
}

/**
 * String
 * @param  val 
 * @returns 
 */
export function isString(val: any) {
    return typeof val == "string" || val instanceof String
}

/**
 * "true""false"
 * @param  val 
 * @returns 
 */
export function isBoolean(val: string) {
    return /^true|false$/i.test(val)
}

/**
 * undefined
 * @param  val 
 * @returns 
 */
export function isUndefined(val: any) {
    return val === undefined || typeof val === "undefined"
}

/**
 *  null  undefined
 * @param  val 
 * @returns 
 */
export function isNullOrUndefined(val: any) {
    return isNull(val) || isUndefined(val)
}

/**
 * 
 * @param  val 
 * @returns 
 */
export function isUpper(val: string) {
    return !!(val && val.toUpperCase() === val)
}

/**
 * 
 * @param  val 
 * @returns 
 */
export function isLower(val: string) {
    return !!(val && val.toLowerCase() === val)
}

/**
 * Date
 * @param val 
 * @returns 
 */
export function isDate(val: any) {
    return !!(val && Object.prototype.toString.call(val) === "[object Date]" && !isNaN(val))
}

/**
 * Error
 * @param val 
 * @returns 
 */
export function isError(val: any) {
    return !!(val && val instanceof Error)
}

/**
 * value
 * @param value 
 * @param args 
 */
export function isIn<T>(value: T, ...args: T[]): boolean {
    if (!args || !args.length) {
        return false
    }
    return args.includes(value)
}

/**
 * value
 * @param value 
 * @param args 
 */
export function isInIgnoreCase(value: string, ...args: string[]): boolean {
    if (!args || !args.length) {
        return false
    }
    if (!isString(value)) {
        return args.includes(value)
    }
    return !!args.find((k) => (k || "").toUpperCase() === value.toUpperCase())
}
