/**
 * 
 */
export class SelectItem<ValueType, ExtendType> {
    constructor(text: string, value: ValueType, isSelected: boolean = false, extend?: ExtendType) {
        this.text = text
        this.value = value
        this.isSelected = isSelected
        this.extend = extend
    }
    /**
     * 
     */
    text: string = ""
    /**
     * 
     */
    isSelected = false
    /**
     * value
     */
    value: ValueType
    /**
     * 
     */
    extend?: ExtendType
}